'use client';

import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Autoplay, EffectCards } from 'swiper/modules';
import { Star, Quote } from 'lucide-react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/effect-cards';
import { motion } from 'framer-motion';

interface Review {
    id: string;
    author: string;
    rating: number;
    comment: string;
    date: string;
}

interface ReviewsSliderProps {
    reviews: Review[];
}

export const ReviewsSlider: React.FC<ReviewsSliderProps> = ({ reviews }) => {
    return (
        <div className="w-full max-w-5xl mx-auto px-4 py-12">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6 }}
                className="text-center mb-12"
            >
                <h2 className="text-4xl font-bold font-poppins text-neutral-dark mb-4">Voices of Travelers</h2>
                <p className="text-gray-600">Stories from those who wandered before you.</p>
            </motion.div>

            <Swiper
                modules={[Pagination, Autoplay]}
                spaceBetween={30}
                slidesPerView={1}
                pagination={{ clickable: true }}
                autoplay={{ delay: 5000, disableOnInteraction: false }}
                breakpoints={{
                    640: { slidesPerView: 1 },
                    768: { slidesPerView: 2 },
                    1024: { slidesPerView: 3 },
                }}
                className="w-full pb-16"
            >
                {reviews.map((review) => (
                    <SwiperSlide key={review.id} className="h-full">
                        <div className="bg-white p-8 rounded-2xl shadow-lg border border-neutral-100 h-full flex flex-col relative overflow-hidden group hover:shadow-xl transition-shadow duration-300">
                            <Quote className="absolute top-4 right-4 text-primary/10 w-16 h-16 transform -rotate-12 group-hover:scale-110 transition-transform" />

                            <div className="flex items-center gap-1 mb-4">
                                {[...Array(5)].map((_, i) => (
                                    <Star
                                        key={i}
                                        className={`w-4 h-4 ${i < review.rating ? 'fill-primary text-primary' : 'text-gray-300'}`}
                                    />
                                ))}
                            </div>

                            <p className="text-gray-600 mb-6 italic leading-relaxed flex-grow relative z-10">
                                "{review.comment}"
                            </p>

                            <div className="flex items-center mt-auto border-t border-gray-100 pt-4">
                                <div className="w-10 h-10 rounded-full bg-gradient-to-br from-primary to-accent flex items-center justify-center text-white font-bold text-sm shadow-md">
                                    {review.author.charAt(0)}
                                </div>
                                <div className="ml-3">
                                    <p className="font-bold text-sm text-neutral-dark">{review.author}</p>
                                    <p className="text-xs text-gray-400">{review.date}</p>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>
                ))}
            </Swiper>
        </div>
    );
};
